.container{
    width: 607px;
    height: 829px;
    margin: auto;
    margin-top: 75px;
    border-radius: 12px;
    background-image: linear-gradient(180deg, #130754 0%,#3b2f80 100%);
}

.top-bar{
    display: flex;
    justify-content: center;
    gap: 14px;
    padding-top: 60px;
}
.top-bar input{
    display: flex;
    width: 326px;
    height: 78px;
    border: none;
    outline: none;
    background: #ebfffc;
    border-radius: 40px;
    padding-left: 40px;
    font-size: 20px;
    font-weight: 400px;
    color: #626262;
}
.search-icon{
    display: flex;
    justify-content: center;
    background: #ebfffc;
    width: 78px;
    height:78px;
    border-radius: 40px;
  align-items: center;
  cursor: pointer;
}
.weather-img{
    margin-top: 29px;
    justify-content: center;
    display: flex;
}
.weather-temp{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 120px;
    font-weight: 400px;
}
.weather-loc{
    display: flex;
    color: white;
    font-size: 60px;
    font-weight: 400px;
    justify-content:center;
}
.data-container{
    display: flex;
    justify-content: center;
    color: white;
    margin-top: 50px;
}
.element{
    display: flex;
    margin: auto;
    align-items: flex-start;
    gap: 12px;
}
.data{
    font-size: 34px;
    font-weight: 400px;
}
.text{
    font-size: 20px;
    font-weight: 400;
}
.icon{
    margin-top: 30px;
}